body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted;
  border-radius: 50%;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.stopwatch__yellow-dot {
  z-index: 2;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch__seconds-pointer {
  z-index: 1;
  box-sizing: border-box;
  transform-origin: 100%;
  background-color: #2c8000;
  width: 38vmin;
  height: 1.5vmin;
  margin-inline: 2vmin;
  animation: 60s linear infinite rotate-anim;
  position: absolute;
}

.stopwatch__minutes-pointer {
  z-index: 0;
  box-sizing: border-box;
  transform-origin: 100%;
  background-color: #0700ff;
  width: 20vmin;
  height: 3vmin;
  margin-inline: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-anim;
  position: absolute;
}

@keyframes rotate-anim {
  from {
    transform: rotate(90deg);
  }

  to {
    transform: rotate(450deg);
  }
}

.stopwatch--speed-up .stopwatch__minutes-pointer {
  animation: 600s steps(60, end) infinite rotate-anim;
}

.stopwatch--speed-up .stopwatch__seconds-pointer {
  animation: 10s linear infinite rotate-anim;
}

